.container .window{position:absolute;overflow:auto;top:0%;left:0;height:90%;width:100%;background-color:oldlace;padding-bottom:1rem;border-bottom:2px solid rgba(34,38,41,0.6)}.container .window-hide{display:none}.container .window__navigation{display:-webkit-flex;display:flex;-webkit-flex-direction:row-reverse;flex-direction:row-reverse;-webkit-justify-content:space-between;justify-content:space-between;padding:.3rem 1rem;border-bottom:2px solid rgba(34,38,41,0.6)}.container .window__navigation:hover{cursor:-webkit-grab;cursor:grab}.container .window__navigation h1{-webkit-align-self:center;align-self:center;color:black}.container .window__navigation button{background-color:transparent;border:none;color:black}.container .window__navigation button:focus{outline:none}.container .window__navigation button:hover{cursor:pointer}.container .window__content{padding:0 0.5rem}.container .window__content .loader{display:block;height:50px;width:50px;margin:1rem auto;border:4px rgba(0,0,0,0.25) solid;border-top:4px black solid;border-bottom:4px black solid;border-radius:50%;-webkit-animation:spin 1s infinite linear;animation:spin 1s infinite linear}@-webkit-keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(359deg);transform:rotate(359deg)}}@keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(359deg);transform:rotate(359deg);-webkit-transform:rotate(359deg);transform:rotate(359deg)}}.container .window__content__description{color:black;font-size:18px;font-style:italic;line-height:2rem}.container .window__content__project{padding:2%;margin:2% 0;background:rgba(255,255,255,0.35);box-shadow:0 8px 32px 0 rgba(31,38,135,0.3);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:10px;border:1px solid rgba(255,255,255,0.6)}.container .window__content__project__links{position:absolute;top:0.5rem;right:0}.container .window__content__project__links a{margin:0.5rem;color:black}.container .window__content__project p{margin:1% 0}.container .window__content__project__stack{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.container .window__content__project__stack p{font-style:italic;margin:1% 2%}@media (min-width: 900px){.container .window{top:5%;bottom:5%;left:20%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:35rem;background:oldlace;border-radius:10px;border:2px solid rgba(34,38,41,0.6)}.container .window__navigation{border-bottom:2px solid rgba(34,38,41,0.6)}.container .window__content{padding:0 1rem}.container .window::-webkit-scrollbar-track{background-color:transparent}.container .window::-webkit-scrollbar{background-color:transparent}.container .window::-webkit-scrollbar-thumb{background-color:transparent;border-radius:10px}}

